import React from 'react';
import styled, {css} from 'styled-components';

const CategoryBlock = styled.div`
    border-top:80px solid #222;
    .row{display:flex; padding-top:50px; justify-content:center;}
`
// props.active가 true일 경우 새로운 css를 적용하라.
const Cate = styled.div`
    color:#000; padding:0 20px; margin:0 20px;
    cursor:pointer;
    // &+&:margin-left:20px;
${
    props => props.active && css`
        color:green;
        border-bottom:2px solid #000;
        font-weight:bold;
    `
}
`

const categories = [
    {name:'all', text:'전체보기'},
    {name:'business', text:'비지니스'},
    {name:'entertainment', text:'엔터테인먼트'},
    {name:'health', text:'건강'},
    {name:'science', text:'과학'},
    {name:'sports', text:'스포츠'},
    {name:'technology', text:'기술'}
]

const Category = ({category, onSelect}) => {
    return (
        <CategoryBlock>
            <div className="row">
                {
                    categories.map( (data) => (<Cate key={data.name} active={category === data.name} onClick={() => onSelect(data.name)}>{data.text}</Cate>) )
                }
            </div>
        </CategoryBlock>
    );
};

export default Category;