import React, {useState, useEffect} from 'react';
import axios from 'axios';
import NewsItem from './NewsItem';
import styled from 'styled-components';
import {CgSandClock} from 'react-icons/cg';

// 내부스타일 잡는 방식
const NewsSectionBlock = styled.div`
    .row{padding-top:80px; display:flex; flex:1 1; flex-wrap:wrap;}
`;

const NewsSection = ({category}) => {
    const [articles, setArticles] = useState(null);
    const [loading, setLoading] = useState(false);
    const getNews = async () => {
        // setLoading(true);
        const query = category ==='all'? '': `&category=${category}`;
        const response = await axios.get(`http://newsapi.org/v2/top-headlines?country=kr${query}&apiKey=440353dc7e7947ccbff0c5d2394a1890`);
        // console.log(response);
        setLoading(true);
        setArticles(response.data.articles);
    }
    useEffect(
        () => {
            getNews()
        }, [category]
    )
    if (!loading) {
        return (<NewsSectionBlock>
            <CgSandClock />
        </NewsSectionBlock>)
    }

    // article에 내용이 없을 경우 null 출력
    if(!articles){
        return <NewsSectionBlock></NewsSectionBlock>;
    } else {
        return (
            <NewsSectionBlock>
                <div className="row">
                    {
                        articles.map( (article) => ( <NewsItem key={article.url} article={article} /> ) )
                    }
                </div>
            </NewsSectionBlock>
        );
    }
};

export default NewsSection;