import React from 'react';
import styled from 'styled-components';
import {Link} from 'react-router-dom';
import Button from './Button';

const MemberFormBlock = styled.div`
    max-width:500px; margin-left:auto; margin-right:auto;
    h3{text-align:center; font-size:40px; margin:30px 0;}
`
const StyledInput = styled.input`
    padding:15px 10px; border-radius:10px; border:1px solid #555;
    width:100%; margin-bottom:20px;
`
const MemberFoot = styled.div`
    margin-top:20px; text-align:right;
    a{color:blue; text-decoration:underline;}
`

const textMap = {
    login:'로그인',
    join:'회원가입'
}

const MemberForm = ({type}) => {
    const text = textMap[type]
    return (
        <MemberFormBlock>
            <h3>{text}</h3>
            <form>
                <StyledInput name="userid" placeholder="아이디를 입력하세요." type="text" />
                <StyledInput name="password" placeholder="비밀번호를 입력하세요." type="password" />
                {
                    // type이 join일 경우(join페이지에서만)에만 하단 인풋이 추가됨
                    type === 'join' && (
                        <StyledInput name="passwordok" placeholder="비밀번호를 한번 더 입력하세요." type="password" />
                    )
                }
                <Button>{text}</Button>
            </form>
            <MemberFoot>
                {
                    type === 'login' ? (
                        <Link to='/join'>아직 회원이 아니신가요?</Link>
                    ) : (
                        <Link to='/login'>이미 회원가입을 하셨다면..</Link>
                    )
                }
            </MemberFoot>
        </MemberFormBlock>
    );
};

export default MemberForm;