import React from 'react';
import styled from 'styled-components';

const StyledButton = styled.button`
    width:100%; padding:15px 0; color:#fff; background:#999; border-radius:10px; border:none;
    &:hover{background:#333;}
`

const Button = (props) => {
    return (
        // {props} 형식으로 쓰면 에러가 발생하시 때문에 스프레드 형식으로 표현
        <StyledButton {...props} />
    );
};

export default Button;