import React, { useEffect } from 'react';
import Header from '../components/Header';
import Footer from '../components/Footer';
import styled from 'styled-components';

const NewsDetailBlock = styled.div`
    border-top:80px solid #111;
    p:nth-child(1){
        color:#111; font-size:30px; text-align:center; padding:30px 0; font-weight:bold;
    }
    p:nth-child(2){
        img{max-width:1080px; box-shadow:1px 1px 1px #aaa; border-radius:5px;}
    }
    p:nth-child(3){
        padding:20px 0;
    }
    
`;


const NewsDetail = (props) => {
    const {location, history} = props;

    // return null 값일 경우 메인페이지 이동시키기
    useEffect(
        () => {
            if(location.state === undefined){
                history.push('/')
            }
        },[]
    )

    if (location.state){
        return (
            <div className="wrap">
                <Header />
                <NewsDetailBlock>
                    <div className="row">
                        <p>{location.state.title}</p>
                        <p><img src={location.state.urlToImage} alt={location.state.title} /></p>
                        <p>{location.state.description}</p>
                    </div>
                </NewsDetailBlock>
                <Footer />
            </div>
        );
    } else {
        return null;
    }
};

export default NewsDetail;